html {
    font-size: 32px;
}

p {
    font-size: 1rem;
    color: var(--black-white);
}

footer {
    width: 90%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    position: relative;
    margin: 10% auto auto;
}

section > .content {
    margin-top: 18%;
    margin-left: 5%;
    width: 70%;
    overflow: hidden;
}

.content > h1 {
    font-size: .9rem;
}

.content > p {
    font-size: .7rem;
}

.home-image {
    width: 90%;
    height: 50%;
    margin: 10% auto 0;
}
